'use client'

import { useState, useEffect, useRef } from 'react'
import Link from 'next/link'
import ScrollToTop from './ScrollToTop'

interface Translations {
  [key: string]: any // 根据你的翻译文件结构调整类型
}

export default function MobileLayoutClient({
  lang,
  translations: t,
  children,
}: {
  lang: string
  translations: Translations
  children: React.ReactNode
}) {
  const [showStickyHeader, setShowStickyHeader] = useState(false)
  const [showModal, setShowModal] = useState(false)
  const settingsRef = useRef<HTMLDivElement>(null)
  const [selectedLanguage, setSelectedLanguage] = useState(lang)

  // 处理页面滚动事件
  useEffect(() => {
    const handleScroll = () => {
      const scrollPosition = window.scrollY
      const footer = document.querySelector('footer')
      const footerTop = footer
        ? footer.getBoundingClientRect().top + window.scrollY
        : Infinity

      if (
        scrollPosition > 500 &&
        scrollPosition + window.innerHeight < footerTop + 1210
      ) {
        setShowStickyHeader(true)
      } else {
        setShowStickyHeader(false)
      }
    }

    window.addEventListener('scroll', handleScroll)
    return () => window.removeEventListener('scroll', handleScroll)
  }, [])

  const closeModal = () => setShowModal(false)
  const openModal = () => setShowModal(true)

  const handleLanguageChange = (event: React.ChangeEvent<HTMLSelectElement>) => {
    const newLang = event.target.value
    setSelectedLanguage(newLang)
    window.location.href = `/${newLang}${window.location.pathname.replace(/^\/[^/]+/, '')}`
  }

  // 根据语言切换字体
  const fontClass =
    selectedLanguage === 'zh'
      ? 'font-black'
      : selectedLanguage === 'en'
      ? 'font-newRoman'
      : 'font-default'

  return (
    <div className={fontClass}>
      {showStickyHeader && (
        <div className="sticky-header">
          <div className="sticky-logo">
            <Link href={`/${lang}/mobile/top`}>XFuture</Link>
          </div>
          <div className="sticky-header-box">
            <div
              ref={settingsRef}
              className="sticky-settings"
              onClick={openModal}
            >
              <div className="settings-icon">
                <div className="line"></div>
                <div className="line"></div>
                <div className="line"></div>
              </div>
            </div>
            <Link href={`/${lang}/mobile/route/recruit`} className="adopt-link">
              <div className="sticky-adopt">
                <img src="/images/mobile/group597.png" alt="Adopt Icon" />
                <span>{t.adoptpage_m}</span>
              </div>
            </Link>
            <Link href={`/${lang}/mobile/route/contact`} className="consult-link">
              <div className="sticky-consult">
                <img src="/images/mobile/group1254.png" alt="Consult Icon" />
                <span>{t.contactpage_m}</span>
              </div>
            </Link>
          </div>
        </div>
      )}

      <header className="header">
        <div className="logo">
          <Link href={`/${lang}/mobile/top`}>XFuture</Link>
        </div>
        <div className="adopt_consult">
          <Link href={`/${lang}/mobile/route/recruit`} className="adopt-link">
            <div className="adopt">
              <img src="/images/mobile/group597.png" alt="Adopt Icon" />
              <span>{t.adoptpage_m}</span>
            </div>
          </Link>
          <Link href={`/${lang}/mobile/route/contact`} className="consult-link">
            <div className="consult">
              <img src="/images/mobile/group1254.png" alt="Consult Icon" />
              <span>{t.contactpage_m}</span>
            </div>
          </Link>
        </div>
      </header>

      <main>{children}</main>

      <footer className="bottom">
        <div className="bottom-box">
          <div className="address-box">
            <span>{t.address}</span>
          </div>
          <div className="phone-box">
            <img src="/images/mobile/group569.png" alt="" />
            <div>03-6826-0688</div>
          </div>
          <div className="button-box">
            <Link href={`/${lang}/mobile/route/recruit`} className="adopt-link">
              <button className="button">{t.recruit}</button>
            </Link>
            <Link href={`/${lang}/mobile/route/contact`} className="consult-link">
              <button className="button">{t.Contact}</button>
            </Link>
          </div>
          <div className="nav-box">
            <nav>
              <table className="nav-table">
                <tbody>
                  <tr>
                    <td>
                      <Link href={`/${lang}/mobile/top`} className="active">
                        {t.toppage}
                      </Link>
                    </td>
                  </tr>
                  <tr>
                    <td>
                      <Link href={`/${lang}/mobile/route/introduce`}>
                        {t.introducepage}
                      </Link>
                    </td>
                  </tr>
                  <tr>
                    <td>
                      <Link href={`/${lang}/mobile/route/service`}>
                        {t.servicepage}
                      </Link>
                    </td>
                  </tr>
                  <tr>
                    <td>
                      <Link href={`/${lang}/mobile/route/news`}>
                        {t.newspage}
                      </Link>
                    </td>
                  </tr>
                </tbody>
              </table>
            </nav>
          </div>
          <span className="nav-c">{t.copyright}</span>
        </div>
        <ScrollToTop />
      </footer>

      {showModal && (
        <div className="custom-modal">
          <div className="modal-content">
            <button className="modal-close" onClick={closeModal}>
              ×
            </button>
            <div className="language-selector">
              <select
                id="language"
                value={selectedLanguage}
                onChange={handleLanguageChange}
                className="form-select"
              >
                <option value="ja">日本語</option>
                <option value="en">English</option>
                <option value="zh">简体中文</option>
              </select>
            </div>
            <table className="modal-table">
              <tbody>
                <tr>
                  <td>
                    <Link href={`/${lang}/mobile/top`} className="active">
                      {t.toppage}
                    </Link>
                  </td>
                </tr>
                <tr>
                  <td>
                    <Link href={`/${lang}/mobile/route/introduce`}>
                      {t.introducepage}
                    </Link>
                  </td>
                </tr>
                <tr>
                  <td>
                    <Link href={`/${lang}/mobile/route/service`}>
                      {t.servicepage}
                    </Link>
                  </td>
                </tr>
                <tr>
                  <td>
                    <Link href={`/${lang}/mobile/route/news`}>
                      {t.newspage}
                    </Link>
                  </td>
                </tr>
              </tbody>
            </table>
            <div className="mobile-phone-box">
              <img src="/images/mobile/group569.png" alt="" />
              <span>03-6826-0688</span>
            </div>
            <div className="mobile-button-box">
              <Link href={`/${lang}/mobile/route/recruit`} className="adopt-link">
                <button className="topbutton">{t.recruit}</button>
              </Link>
              <Link href={`/${lang}/mobile/route/contact`} className="consult-link">
                <button className="topbutton">{t.Contact}</button>
              </Link>
            </div>
          </div>
        </div>
      )}
    </div>
  )
}